import {useCallback} from "react";
import Particles from "react-tsparticles";
import type {Container, Engine} from "tsparticles-engine";
import {loadFull} from "tsparticles";
import styles from "../particle.module.scss"


export const ParticleCollision = () => {

    const particlesInit = useCallback(async (engine: Engine) => {
        console.log(engine);

        // you can initialize the tsParticles instance (engine) here, adding custom shapes or presets
        // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
        // starting from v2 you can add only the features you need reducing the bundle size
        await loadFull(engine);
    }, []);

    const particlesLoaded = useCallback(async (container: Container | undefined) => {
        await console.log(container);
    }, []);

    return (<Particles
            id="tsparticles"
            init={particlesInit}
            loaded={particlesLoaded}
            className={styles.particle}
            options={
                {
                    "fullScreen": {
                        "enable": false,
                        "zIndex": -1
                    },
                    "particles": {
                        "number": {
                            "value": 40,
                            "density": {
                                "enable": true,
                                "area": 800
                            }
                        },
                        "color": {
                            "value": "#383838"
                        },
                        "shape": {
                            "type": "circle"
                        },
                        "opacity": {
                            "value": 0.5,
                            "random": true,
                            "animation": {
                                "enable": true,
                                "speed": 0.1,
                                "minimumValue": 0.1,
                                "sync": false
                            }
                        },
                        "size": {
                            "value": 5,
                            "random": {
                                "enable": true,
                                "minimumValue": 3
                            },
                            "animation": {
                                "enable": true,
                                "speed": 1,
                                "minimumValue": 3,
                                "sync": true
                            }
                        },
                        "lineLinked": {
                            "enable": true,
                            "distance": 150,
                            "color": "#797979",
                            "opacity": 0.3,
                            "width": 1
                        },
                        "move": {
                            "bounce": true,
                            "enable": true,
                            "speed": 1,
                            "direction": "none",
                            "random": false,
                            "straight": false,
                            "out_mode": "bounce",
                            "attract": {
                                "enable": false,
                                "rotateX": 600,
                                "rotateY": 1200
                            }
                        }
                    },
                    "interactivity": {
                        "events": {
                            "onHover": {
                                "enable": true,
                                "mode": "bubble",
                                "parallax": {
                                    "enable": false,
                                    "force": 60,
                                    "smooth": 10
                                }
                            },
                            "onClick": {
                                "enable": true,
                                "mode": "repulse"
                            },
                            "resize": true
                        },
                        "modes": {
                            "grab": {
                                "distance": 200,
                                "line_linked": {
                                    "opacity": 1
                                }
                            },
                            "bubble": {
                                "distance": 400,
                                "duration": 2,
                                "opacity": 1,
                                "speed": 3,
                            },
                            "repulse": {
                                "distance": 200,
                                "duration": 7,
                            },
                            "push": {
                                "particles_nb": 4
                            },
                            "remove": {
                                "particles_nb": 2
                            },

                        }
                    },
                    "detectRetina": true,
                }
            }
        />

    );
};
